import styled from '@emotion/styled';
import { Typography, alpha } from '@mui/material';
import { Box } from '@mui/system';
import React, { useEffect, useState } from 'react'
import { flickerIn1 } from '../../theme/flickerIn1';



const SkillBox = styled(Box)(({skill}) =>({
  padding:'10px',
  border:'4px lightgray solid',
  marginTop:'7%',
  marginLeft:'25%',
  width:'50%',
  marginBottom:'11.5%',
  background: alpha('#9e9e9e',0.13),
  animation: skill && `${flickerIn1} 2s linear both`,
  textAlign:'center',
  '@media (max-width: 600px)': {
    width: '70%',
    marginTop:'25%',
    marginLeft:'15%'
  },

}));

const Skills = () => {
  const [skill,setSkill] = useState(false)
  useEffect(() =>{
    setTimeout(() =>{
      //animation
      setSkill(true);
    },500)
  },[])
  return (
    <>    <SkillBox skill={skill.toString()}>
       <img id='js' alt="svgImg" src="data:image/svg+xml;base64,"/>
       <img id='java' width='100'  src="https://img.icons8.com/fluency/96/java-coffee-cup-logo.png" alt="java-coffee-cup-logo"/>
       <img id='html' alt="svgImg" src="data:image/svg+xml;base64,"/>
       <img id='css' alt="svgImg" src="data:image/svg+xml;base64,"/>
       <img id='spring' alt="svgImg" src="data:image/svg+xml;base64,"/>
       <img id='react' alt='react' src="data:image/png;base64,"></img>
       <img alt='laravel' id='laravel' src="data:image/png;base64,"></img>
       
       
        <Typography variant='h6' sx={{color:'#e0f2f1',fontWeight:'bold',paddingLeft:'15px',marginTop:'12px'}}>
        My knowledge and skills encompass various technologies, including HTML, CSS, Javascript, React, Material UI, Java, Spring boot and Laravel. I possess strong collaboration abilities and work well in a team environment.I also pay attention to designing robust and efficient backend systems. 
        </Typography>
        
    </SkillBox>
    
    </>

  )
}

export default Skills
